@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	margin: 0;
	padding: 0;
	font-family: 'Source Sans Pro', sans-serif;
	background-color: #f5f5f5;
}

.btn {
	font-size: 16px;
	line-height: 20px;
	border-radius: 8px;
	padding: 10px 16px;
	text-decoration: none;
	display: inline-block;
	cursor: pointer;
}

.btn-default {
	color: #fff;
	background-color: #5784BA;
}

.btn-secondary {
	color: #5784BA;
	background-color: #B6D8F2;
}

.btn:hover, button:hover {
	box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
}

.facebook_login_btn button {
	color: #fff;
	font-size: 16px;
	line-height: 20px;
	border-radius: 8px !important;
	background-color: #5784BA;
	padding: 10px 16px;
	text-decoration: none;
	display: inline-block;
	cursor: pointer;
	border: none;
	font-family: 'Source Sans Pro', sans-serif;
	font-weight: normal;
	text-transform: none;
}
.app{padding:40px;color:#5784ba;min-height:100vh;position:relative}.app .app_board{padding:16px 24px;border-radius:16px;background:linear-gradient(to left, #B6D8F2, #F4CFDF);position:absolute;z-index:1;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:100%;max-width:630px;height:355px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;text-align:center}.app .app_board .poke_1,.app .app_board .poke_2{position:absolute;z-index:2;width:48px;height:48px;top:-48px}.app .app_board .poke_1{right:0;-webkit-animation-name:poke1;animation-name:poke1;-webkit-animation-duration:15s;animation-duration:15s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}.app .app_board .poke_2{left:0;-webkit-animation-name:poke2;animation-name:poke2;-webkit-animation-duration:15s;animation-duration:15s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes poke1{0%{right:0}50%{right:calc(100% - 48px)}100%{right:0}}@keyframes poke1{0%{right:0}50%{right:calc(100% - 48px)}100%{right:0}}@-webkit-keyframes poke2{0%{left:0}50%{left:calc(100% - 48px)}100%{left:0}}@keyframes poke2{0%{left:0}50%{left:calc(100% - 48px)}100%{left:0}}
.header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;position:relative}.header .header_logo{display:-webkit-flex;display:flex;font-size:40px}.header .header_logo div:first-child{color:#5784ba}.header .header_logo div:nth-child(2){color:#9ac8eb}.header .header_logo div:nth-child(3){color:#f4cfdf}.header .header_logo div:nth-child(4){color:#b6d8f2}.header .header_logo div:last-child{color:#f4cfdf}.header .header_icon{width:64px;height:64px;position:absolute;z-index:1;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);background-size:cover;background-repeat:no-repeat}.header .header_icon.auth{border-radius:50%;overflow:hidden;box-shadow:0 1px 6px rgba(0,0,0,.2)}.header .header_menu{display:-webkit-flex;display:flex}.header .header_menu .header_menu_item{margin-right:16px;border-bottom:2px solid;cursor:pointer}.header .header_menu .header_menu_item:last-child{margin-right:0}
.start_page .title{font-size:24px;margin-bottom:16px}.start_page .score{font-size:18px;margin-bottom:8px}.start_page .timer{font-size:18px;margin-bottom:24px}.start_page .code{font-size:28px;margin-bottom:24px}.start_page .how_to_play{font-size:16px;line-height:20px;margin-bottom:16px}.start_page .how_to_play i{margin-right:2px;text-decoration:underline}
.finish_page .title{font-size:28px;margin-bottom:16px}.finish_page .title span:nth-child(1){color:#5784ba}.finish_page .title span:nth-child(2){color:#9ac8eb}.finish_page .title span:nth-child(3){color:#f4cfdf}.finish_page .title span:nth-child(4){color:#9ac8eb}.finish_page .title span:nth-child(5){color:#f7f6cf}.finish_page .title span:nth-child(6){color:#9ac8eb}.finish_page .title span:nth-child(7){color:#f4cfdf}.finish_page .title span:nth-child(8){color:#f7f6cf}.finish_page .level{font-size:16px;color:#5784ba;margin-bottom:16px}.finish_page .score{font-size:24px;color:#5784ba;margin-bottom:24px;padding:8px 16px;border-radius:0px;border:1px solid;text-transform:uppercase}.finish_page .note{font-size:16px;line-height:20px;margin-bottom:16px}
.play_page .title{font-size:24px;margin-bottom:16px}.play_page .score{font-size:18px;margin-bottom:8px}.play_page .timer{font-size:18px;margin-bottom:24px}.play_page .code{font-size:28px;margin-bottom:24px}.play_page .form_control input{font-size:28px;border:none;border-bottom:2px solid;background:transparent;outline:none;text-align:center;padding:8px;display:inline-block;color:#5784ba}
.ranking_board{position:fixed;z-index:9;width:300px;max-height:75%;top:100px;right:40px;background-color:#fff;border-radius:16px;box-shadow:0px 0px 2px rgba(0,0,0,.14),0px 2px 2px rgba(0,0,0,.12);padding:24px 16px;overflow-y:auto;background-color:#b6d8f2}.ranking_board .not_found{font-size:18px;line-height:24px;text-align:center}.ranking_board .player{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin-bottom:16px}.ranking_board .player:last-child{margin-bottom:0}.ranking_board .player .player_number{font-size:16px;margin-right:8px}.ranking_board .player .player_avatar{margin-right:16px;width:40px;height:auto;border-radius:50%}.ranking_board .player .player_name{margin-right:16px;-webkit-flex:1 1;flex:1 1}.ranking_board .player:nth-child(1),.ranking_board .player:nth-child(2),.ranking_board .player:nth-child(3){font-weight:bold}.ranking_board .player:nth-child(1) .player_number,.ranking_board .player:nth-child(2) .player_number,.ranking_board .player:nth-child(3) .player_number{color:#5784ba}
